@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
:root {
    --white: #F8F8FF;
    --gray: #808080;
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    margin: 0;
    font-family: 'Montserrat', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

footer {
    position: absolute;
    bottom: 0;
    width: 100%;
    padding: 1vh;
    margin: auto;
    color: #F8F8FF;
    color: var(--white);
    background-color: #808080;
    background-color: var(--gray);
    text-align: center;
}
header {
    background-color: var(--gray);
}

h1 {
    color: var(--white);
    padding: 1vh;
}


.thumbnail {
    width: 30%;
    display: contents;
}

.visible {
    display: block;
}

.hidden {
    display: none;
}
.slideshow-container {
    max-width: 1000px;
    position: absolute;
    top: 0;
    margin: auto;
}

.current-slide {
    display: block;
    margin: auto;
}

.close {
    float: right;
}

.prev, .next {
    cursor: pointer;
    position: absolute;
    top: 50%;
    padding: 10px 16px;
    color: var(--white);
    font-weight: bold;
    font-size: 14px;
    transition: 0.3s ease;
    border-radius: 50%;
    border-width: 0;
}

.next {
    right: 0;
}

.prev:hover, .next:hover {
    background-color: var(--gray);
}

.prev:focus, .next:focus {
    outline: none;
}

.number {
    color: var(--white);
    font-size: 10px;
    padding: 5px;
    position: absolute;
    top: 0;
}

.caption {
    color: var(--gray);
    font-size: 12px;
}


